//localStorage.clear();
let initialState;
initialState = localStorage.getItem('offers');

if(!initialState) {
    console.log(initialState);
    console.log('nic w localstorage');
    initialState = [];
} else {
    initialState = JSON.parse(initialState);
}
console.log('inmital srtate');
console.log(initialState);
const offersReducer = (state = initialState, action) => {
    switch(action.type) {
        case 'ADD_OFFER':
            state.unshift(action.payload);

            //define offers array
            let offers = [];
            // get offers from storage
            let offersFromStorage = localStorage.getItem('offers');
            if(offersFromStorage) {
                offersFromStorage = JSON.parse(offersFromStorage);
                console.log('offers from storage', offersFromStorage);
                offers = offersFromStorage;
                offers.unshift(action.payload);
            } else {
                offers.unshift(action.payload);
            }

            localStorage.setItem('offers', JSON.stringify(offers));          
            return state;
            break;
        default:
            return state;
    }
}

export default offersReducer;